export default{
	data(){
			
			
		return{
			search:"",
			list:[],
			TabCur: 0,
			//scrollLeft: 0
			scrolllef:[{"name":"综合"},
			{"name":"销量"},
			{"name":"价格"},
			]
		}
	},
	onLoad() {
		this.requessts();
	},
	methods:{
	requessts:function(){
		uni.request({
			url:"http://localhost:58793/Api/Deafalut/AllContent?Shop_Information_Name="+this.search,
			method:"GET",
			//header:{'token':token},
			success: (res) => {
				console.log(res.data)
				this.list=res.data;
				
			}
		})
	},
	tabSelect(e) {
		this.TabCur = e.currentTarget.dataset.id;
		if(	this.TabCur==0)
		{
			this.list=this.list;
		}
		else if(this.TabCur==1)
		{
		for (let i=0; i<this.list.length; i++) {
			this.list[i].sort();
		}
		}
		
	},
	sousuo(){
		this.requessts();
	},
	fellei(){
		console.log("11")
		uni.navigateTo({
			url:"../fenlei/fenlei"
		})
	}
	
	
		
	}
}

